export default {
  props: {
    source: {
      type: Array,
      default: [],
    },
    css: {
      type: Object,
      default: {},
    },
  },
  data() {
    return {
      showAutocomplete: false
    }
  },
  methods: {
    selected(payload) {
      console.log(payload);
      this.$emit('selected', payload.selectedObject)
    },
    clearSelection() {
      this.showAutocomplete = !this.showAutocomplete;
      this.$emit('clearSelection')
    }
  },
};
